.daily{
    .header{
        display: flex;
        justify-content: center;
        margin-bottom: 2em;
        letter-spacing: 0.05em;
        span{
            text-decoration: none;
            font-weight: 600;
            font-size: 13px;
            background-color: #d62839;
            color: #fff;
            padding: 4px 21px;
            border-radius: 4px;
        }
    }
    
    .card{
        
        .title{
            text-align: center;
            h2{
                margin: 0.4em 0;
            }
            letter-spacing: 1.4px;
        }
        .date{
            text-align: center;
            color: #aaa;
            letter-spacing: 1.4px;
            margin-bottom: 1em;
            font-size: 13px;
        }
        .content{
            text-align: center;
        }
        hr{
            display: block;
            border: 0;
            margin: 3em auto;
            &::before{
                color: rgba(0, 0, 0, 0.2);
                font-size: 1.1em;
                display: block;
                content: "* * *";
                text-align: center;
            }
        }
    }
    .MuiPagination-ul{
        display: flex;
        justify-content: center;
    }
    
    
}